
<template>
  <div class="page-body">
    <van-nav-bar
      fixed
      title="我的优惠券"
      left-arrow
      @click-left="$router.back()"
    >
    </van-nav-bar>

    <van-tabs v-model="activeName" sticky @click="onClick">
      <van-tab title="未使用" name="isUnused"></van-tab>
      <van-tab title="已使用" name="isUse"></van-tab>
      <van-tab title="已过期" name="isExpire"></van-tab>
    </van-tabs>

    <div class="coupon-list">
      <MyCouponList
        v-if="myCouponList.length"
        :myCouponList="myCouponList"
      ></MyCouponList>
      <div v-else>没有更多了</div>
    </div>
  </div>
</template>

<script>
import { getMyCouponListApi } from '@/api/coupon'
import MyCouponList from '@/components/MyCouponList.vue'

export default {
  name: 'MyCoupon',
  components: {
    MyCouponList
  },

  data () {
    return {
      activeName: 'isUnused',

      page: 1,

      myCouponList: []
    }
  },

  methods: {
    async onClick (name) {
      await this.getMyCouponList(name, this.page)
    },

    async getMyCouponList (dataType, page) {
      const { data: { list } } = await getMyCouponListApi(dataType, page)
      this.myCouponList = list.data
    }

  },

  created () {
    this.getMyCouponList(this.activeName, this.page)
  }

}
</script>

<style lang="less" scoped>
.page-body {
  padding: 48px 0;
}
.coupon-list {
  padding: 10px;
}
</style>
