<template>
  <!-- 优惠券列表 -->
  <div>
    <div class="coupon-item" v-for="(item, index) in myCouponList" :key="index"
    @click="handleClick(item.user_coupon_id)">
      <div
        class="item-wrapper"
        :class="[
          'color-' + (item.state.value ? color[index % color.length] : 'gray'),
        ]"
      >
        <div class="coupon-type">
          {{ CouponType(item.coupon_type) }}
        </div>
        <div class="tip dis-flex flex-dir-column flex-x-center">
          <div v-if="item.coupon_type == 10">
            <span class="f-30">￥</span>
            <span class="money">{{ item.reduce_price }}</span>
          </div>
          <span class="money" v-if="item.coupon_type == 20"
            >{{ item.discount }}折</span
          >
          <span class="pay-line">满{{ item.min_price }}元可用</span>
        </div>
        <div class="split-line"></div>
        <div class="content dis-flex flex-dir-column flex-x-between">
          <div class="title">{{ item.name }}</div>
          <div class="bottom dis-flex flex-y-center">
            <div class="time flex-box">
              <span v-if="item.start_time === item.end_time"
                >{{ item.start_time }} 当天有效</span
              >
              <span v-else>{{ item.start_time }}~{{ item.end_time }}</span>
            </div>
            <div class="receive state">
              <span>{{ item.state.text }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyCouponList',

  props: {
    myCouponList: {
      type: Array
    }
  },

  data () {
    return {
      color: ['red', 'blue', 'violet', 'yellow']
    }
  },

  methods: {
    CouponType (type) {
      if (type === 10) return '满减券'
      else return '折扣券'
    },

    handleClick (couponId) {
      this.$emit('onClickCoupon', couponId)
    }
  }

}
</script>

<style lang="less" scoped>
.dis-flex {
  display: flex;
}
.flex-dir-column {
  flex-direction: column;
}
.flex-x-center {
  justify-content: center;
}
.flex-x-between {
  justify-content: space-between;
}
.flex-y-center {
  align-content: center;
}
.flex-box {
  flex: 1;
}

.coupon-item {
  position: relative;
  overflow: hidden;
  margin-bottom: 11px;
}

.item-wrapper {
  width: 100%;
  display: flex;
  background: #fff;
  border-radius: 8px;
  color: #fff;
  height: 90px;

  .coupon-type {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: 64px;
    padding: 3px 0;
    background: #a771ff;
    font-size: 10px;
    text-align: center;
    color: #ffffff;
    transform: rotate(45deg);
    transform-origin: 32px 32px;
  }

  &.color-blue {
    background: linear-gradient(-125deg, #57bdbf, #2f9de2);
  }

  &.color-red {
    background: linear-gradient(-128deg, #ff6d6d, #ff3636);
  }

  &.color-violet {
    background: linear-gradient(-113deg, #ef86ff, #b66ff5);

    .coupon-type {
      background: #55b5ff;
    }
  }

  &.color-yellow {
    background: linear-gradient(-141deg, #f7d059, #fdb054);
  }

  &.color-gray {
    background: linear-gradient(-113deg, #bdbdbd, #a2a1a2);

    .coupon-type {
      background: #9e9e9e;
    }
  }

  .content {
    flex: 1;
    padding: 15px 10px;
    border-radius: 8px 0 0 8px;

    .title {
      font-size: 16px;
    }

    .bottom {
      .time {
        font-size: 12px;
      }

      .receive {
        height: 23px;
        width: 61px;
        line-height: 23px;
        text-align: center;
        border: 1px solid #fff;
        border-radius: 15px;
        color: #fff;
        font-size: 12px;

        &.state {
          border: none;
        }
      }
    }
  }

  .tip {
    position: relative;
    flex: 0 0 32%;
    text-align: center;
    border-radius: 0 16px 16px 0;

    .money {
      font-weight: bold;
      font-size: 26px;
    }

    .pay-line {
      font-size: 11px;
    }
  }

  .split-line {
    position: relative;
    flex: 0 0 0;
    border-left: 2px solid #fff;
    margin: 0 5px 0 6px;
    background: #fff;

    &:before {
      border-radius: 0 0 8px 8px;
      top: 0;
    }

    &:after {
      border-radius: 8px 8px 0 0;
      bottom: 0;
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 12px;
      height: 6px;
      background: #f7f7f7;
      left: -7px;
      z-index: 1;
    }
  }
}
</style>
